import React, { useState, useEffect } from 'react';

import './App.css';
import { fetchAdvice, fetchAdviceBySearch } from './api/index';
import Cards from './components/Cards/Cards';
import Search from './components/Search/Search';

function App() {
  const [advice, setAdvice] = useState([])
  const [searchResult, setSearchResult] = useState([])


  useEffect(() => {
    fetchAdviceAPI()
  }, [])

  const handleSearch = (value) => {
    searchAdvice(value)
  }
  const searchAdvice = async (text) => {
    const result = await fetchAdviceBySearch(text)
    if (Array.isArray)
      setSearchResult(result)
    else
      setAdvice(result)
  }

  const fetchAdviceAPI = async () => {
    const advice = await fetchAdvice()
    setAdvice(advice)
    setSearchResult([])
  }
  const urlTwitter = "https://twitter.com/IbnuSacad"
  return (
    <div className="App">
      <h1 className="title"><i className="fa fa-assistive-listening-systems"></i> Advice-App</h1>
      <Search handleSearch={handleSearch} />
      <Cards advice={advice} searchResult={searchResult} fetchAdvice={fetchAdviceAPI} />
      <p><a href={urlTwitter} target="__blank" title="My Twitter">@ibnusacad</a></p>

    </div>
  );
}

export default App;
