import axios from 'axios'

const url = 'https://api.adviceslip.com/advice'


export const fetchAdvice = async () => {
    try {
        const { data } = await axios.get(url)
        const { slip: { advice } } = data
        return advice
    } catch (error) {
        console.log(error)
    }
}

const searchUrl = 'https://api.adviceslip.com/advice/search/'
export const fetchAdviceBySearch = async (text) => {
    if (text) {
        try {
            const { data: { slips } } = await axios.get(`${searchUrl}${text}`)
            if (slips)
                return slips
            else
                return [{ advice: 'Sorry, Not advice matching your search Term' }]
        }
        catch (error) {
            console.log(error)
        }
    }
}